import {
	goods
} from '@/config/mixins.js'
export default {
	mixins: [goods],
	name: "goods",
	props: {
		goodsData: {
			required: true,
		}
	},
	methods: {
		//跳转到商品详情页面
		goodsDetail: function(id) {
			let url = '../productDetail/productDetail?goodsId=' + id;
			this.$common.navigateTo(url);
		},
	},
}
